import React from 'react';
import styled from 'styled-components';
import theme from '@style/theme';
import { H3 } from '@style/global';
import { useCVItemQuery } from '../query/itemquery';

const Item = (props) => {
    const GetItemList = () => {
        const items = useCVItemQuery();
        return items;
    };
    const itemList = GetItemList();
    return(
        <ItemBox>
            <H3><b>{props.title}</b></H3>
            {itemList.allCvJson.edges.filter(fortype => fortype.node.type === props.selectType).map(({ node }) => (
                <>
                    <Bloc className="bloc">
                        <Title>{node.title}</Title>
                        <Org>{node.org}</Org>
                        <Location>{node.yrStart} - {node.yrEnd} | {node.location}</Location>
                        <Desc>
                            {node.desc.map((item, i) => {
                                return <LsDesc key={i}>{item}</LsDesc>
                            })}
                        </Desc>
                    </Bloc>
                </>
            ))}
        </ItemBox>
    )
}

export default Item;

const ItemBox = styled.div`
`

const Bloc = styled.div`
  margin: 10px 0;
  padding: 10px 0;
  border-bottom: 1px solid ${theme.color.grayscale.S400};
  :last-of-type{
    border-bottom: none;
  }
`

const Title = styled.div`
    ${theme.fontSize.base};
    font-family: ${theme.font.primary};
    font-weight: bold;
`
const Org = styled.div`
    ${theme.fontSize.base};
`
const Location = styled.div`
    ${theme.fontSize.xs};
    color: ${theme.color.grayscale.S600};
    margin: 5px 0; 
`
const Desc = styled.div`
  margin:0;
`
const LsDesc = styled.div`
    ${theme.fontSize.sm};
    ::before{
        content: "- ";
        color: ${theme.color.grayscale.S500};
    }
`