import { useStaticQuery, graphql } from 'gatsby'

export const useCVItemQuery = () => {
    const CVItemData = useStaticQuery ( graphql`
        query {
            allCvJson {
                totalCount
                edges {
                    node {
                        title
                        yrStart
                        yrEnd
                        location
                        org
                        type
                        desc
                    }
                }
            }
        }
    `);
    return CVItemData;
}
