import { useStaticQuery, graphql } from 'gatsby'


export const useCVHighlightQuery = () => {
    const CVHighlightData = useStaticQuery(graphql`
        query {
            allCvHighLightJson {
                edges {
                    node {
                        type
                        span
                        sum
                        list
                        heading
                    }
                }
            }
        }
    `);
    return CVHighlightData;
}
