import React from 'react';
import styled from 'styled-components'
import theme from '@style/theme'
import { useCVHighlightQuery } from '../query/highlightquery';

const Highlight = (props) => {
    const GetHLList = () => {
        const highlights = useCVHighlightQuery();
        return highlights;
    };
    const hlList = GetHLList();
    return(
        <HighlightBox>
            {hlList.allCvHighLightJson.edges.filter(fortype => fortype.node.type === props.selectType).map(({ node }) => (
                    <>
                        <Sum>{node.sum}</Sum>
                        {node.span ? (
                            <LsIntro>{node.span}</LsIntro>
                        ) : (
                            ''
                        )}
                        {node.list ? (
                            <LsSubIntro><br/>{node.list}</LsSubIntro>
                        ) : (
                            ''
                        )}
                    </>
                ))
            }
        </HighlightBox>
    )
}

export default Highlight;

const HighlightBox = styled.div`
    color: ${theme.color.grayscale.S700};
    margin:10px 0;
    padding: 10px 0;
    @media (max-width: ${theme.screen.md}){
        display: none;
    }
`
const Sum = styled.div`
    ${theme.fontSize.lg};
    margin: 10px 0;
`
const LsIntro = styled.span`
    ${theme.fontSize.base};
    ::before{
        content: "-";
        color: ${theme.color.grayscale.S500};
        padding-right: 5px;
    }
`
const LsSubIntro = styled.span`
    ${theme.fontSize.sm};
    color: ${theme.color.grayscale.S600};
    padding-left: 5px;
`
    